      grid-template-columns: 25px auto 95px;
      border-bottom: 1px solid #F1F1F1;
      height: 55px;
      align-content: center;
      padding-right: 5px;
    }
  }

  .operation-name {
    text-align: left;
  }

  .amount {
    text-align: right;
  }

  .description-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: baseline;
  }

  .payday {
    text-align: left;
    display: inline-block;
    width: 100%;
    font-weight: bold;
    background-color: white;
